.form-element label {
  width: 100%;
  height: 100%;
  pointer-events: none;
  border-bottom: 1px solid #c5fceb;
  font-size: 16px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.form-element label:after {
  content: "";
  width: 100%;
  height: 100%;
  border-bottom: 5px solid #fec24a;
  transition: all .2s .1s;
  position: absolute;
  bottom: -1px;
  left: 0;
  transform: translateX(100%);
}

.content-name {
  padding-bottom: 8px;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.form-element input:focus {
  outline: none;
}

.form-element input:focus + .label-name .content-name, .form-element input:valid + .label-name .content-name {
  color: #fec24a;
  text-shadow: 0 0 5px #fec24a;
  font-size: 12px;
  left: 0;
  transform: translateY(-100%);
}

.form-element input:focus + .label-name:after, .form-element input:valid + .label-name:after {
  transform: translateX(0%);
}

